import React from 'react'
import './Nav.css'
import { Link } from 'react-router-dom'


function Nav() {
    return (
        <div className="nav">
            <div className="heading_container">
                <div className="route_links">
                    {/* links back to home  */}
                    <ul className="route">
                        <li> 
                        <Link to="/">Movie Watcher </Link>
                            
                            </li>

                            {/* link to liked videos */}
                        <li>
                            <Link to="/like">Liked Movies </Link>
                            </li>
                        <li>Disliked</li>
                    </ul>
                </div>
                <div className="user_links">
                    <ul className="link">
                        <li>Join</li>
                        <li> Login</li>
                    </ul>
                </div>
            </div>
            
        </div>
    )
}

export default Nav
