import Liked from './Liked'
import MovieCard from './MovieCard'
function App() {
  return (
    <div className="App">
      <Router>
      <Nav />

        <Switch>
        <Route path="movie_list">
        <MovieCard />
       </Route>
       <Route path="/like">
            
            <Liked />
          </Route>
       </Switch>
      </Router>



     
      
    </div>
  );
}

export default App;
